


















import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CTextLength extends Vue {
  // Data
  private dataText: string = '';
  private dataStrLength: number = 0;
  private dataByteCount: number = 0;

  // Watch
  @Watch('dataText')
  private watchOnStr(newValue: string, orgValue: string) {
    this.calcLength();
  }

  // Methods
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private calcLength(): void {
    this.dataStrLength = this.dataText.length;
    this.dataByteCount = encodeURIComponent(this.dataText).replace(/%../g, 'x').length;
  }
}
